const key2Value = (arr, key) => {
  if (!arr.length) return ''
  return arr.filter((item) => {return item.dKey == key})[0].dValue
}

const id2Name = (arr, id) => {
  if (!arr.length) return ''
  return arr.filter((item) => {return item.id == id})[0].name
}
export {
  key2Value,
  id2Name
}
  