import Vue from 'vue'
import Router from 'vue-router'

const login = () => import('./views/login')
const error = () => import('./views/error')
const main = () => import('./views/main')
// 商品管理
const goods = () => import('./views/goods/goods')
const goodsAdd = () => import('./views/goods/control/add')
// 订单管理
const orders = () => import('./views/orders/orders')
// 活动管理
const campaigns = () => import('./views/campaigns/campaigns')
const campaignsAdd = () => import('./views/campaigns/control/add')
// 公告管理
const notices = () => import('./views/notices/notices')
const noticesAdd = () => import('./views/notices/control/add')
// 媒体管理
const medias = () => import('./views/medias/medias')
const mediasAdd = () => import('./views/medias/control/add')
// 学生管理
const student = () => import('./views/student/index')
const studentIntegral = () => import('./views/student/studentIntegral/integral')
const studentList = () => import('./views/student/studentList/list')
const studentListIntegral = () => import('./views/student/studentList/control/integral')
const studentAdd = () => import('./views/student/studentList/control/add')
// 校区管理
const school = () => import('./views/school/index')
const schoolList = () => import('./views/school/schoolList/list')
const schoolListAdd = () => import('./views/school/schoolList/control/add')
const schoolClass = () => import('./views/school/schoolClass/class')
const schoolClassAdd = () => import('./views/school/schoolClass/control/add')
// 服务管理
const service = () => import('./views/service/index')
const serviceList = () => import('./views/service/serviceList/list')
const serviceDetail = () => import('./views/service/serviceList/control/detail')
const serviceQuestion = () => import('./views/service/serviceQuestion/question')
const serviceQuestionDetail = () => import('./views/service/serviceQuestion/control/detail')
const serviceQuestionAdd = () => import('./views/service/serviceQuestion/control/add')
const serviceQuestionStatistic = () => import('./views/service/serviceQuestion/control/statistic')
// 系统管理
const systemManage = () => import('./views/systemManage/index')
const systemRole = () => import('./views/systemManage/systemRole/systemRole')
const systemAdd = () => import('./views/systemManage/systemRole/control/roleNew')
const systemUser = () => import('./views/systemManage/systemUser/systemUser')
const systemUserAdd = () => import('./views/systemManage/systemUser/control/systemUseradd')
const systemUserInvite = () => import('./views/systemManage/systemUser/control/userInvite')
const systemInviteList = () => import('./views/systemManage/systemInviter/invite')

Vue.use(Router)

export default new Router({
    mode: 'hash',
    base: '/riseapp',
    routes: [
        {
            path: '/error',
            component: error
        },
        {
            path: '/login',
            name: 'login',
            component: login
        },
        {
            path: '/',
            name: 'main',
            component: main,
            children: [
                {
                    path: '/goods',
                    name: 'goods',
                    component: goods, //商品管理
                },
                {
                    path: '/goodsAdd',
                    name: 'goodsAdd',
                    component: goodsAdd,
                },
                {
                    path: '/orders',
                    name: 'orders',
                    component: orders, //订单管理
                },
                {
                    path: '/campaigns',
                    name: 'campaigns',
                    component: campaigns, //活动管理
                },
                {
                    path: '/campaignsAdd',
                    name: 'campaignsAdd',
                    component: campaignsAdd,
                },
                {
                    path: '/notices',
                    name: 'notices',
                    component: notices, //公告管理
                },
                {
                    path: '/noticesAdd',
                    name: 'noticesAdd',
                    component: noticesAdd,
                },
                {
                    path: '/medias',
                    name: 'medias',
                    component: medias, //媒体管理
                },
                {
                    path: '/mediasAdd',
                    name: 'mediasAdd',
                    component: mediasAdd,
                },
                {
                    path: '/student',
                    component: student, //学生管理
                    children: [
                        {
                            path: 'list', //学生列表
                            component: studentList
                        },
                        {
                            path: 'add',
                            component: studentAdd, 
                        },
                        {
                            path: 'detail', //积分明细
                            component: studentListIntegral
                        },
                        {
                            path: 'integral', //积分复核
                            component: studentIntegral
                        },                      
                    ]
                },
                {
                    path: '/school',
                    component: school, //校区管理
                    children: [
                        {
                            path: 'list',
                            component: schoolList //校区列表
                        },
                        {
                            path: 'listAdd',
                            component: schoolListAdd
                        },
                        {
                            path: 'class',
                            component: schoolClass //班级列表
                        },
                        {
                            path: 'classAdd',
                            component: schoolClassAdd
                        }
                    ]
                },
                {
                    path: '/service',
                    component: service, //服务管理
                    children: [
                        {
                            path: 'list',
                            component: serviceList //服务列表
                        },
                        {
                            path: 'detail',
                            component: serviceDetail,
                        },
                        {
                            path: 'question',
                            component: serviceQuestion //问卷列表
                        },
                        {
                            path: 'questionAdd',
                            component: serviceQuestionAdd //问卷新增
                        },
                        {
                            path: 'quesDetail',
                            component: serviceQuestionDetail //问卷详情
                        },
                        {
                            path: 'quesStatistic',
                            component: serviceQuestionStatistic //问卷统计
                        },
                    ]
                },
                {
                    path: 'systemManage',
                    component: systemManage, //系统管理
                    children: [
                        {
                            path: 'role',
                            component: systemRole //权限管理
                        },
                        {
                            path: 'roleAdd',
                            component: systemAdd //权限修改
                        },
                        {
                            path: 'user',
                            component: systemUser //用户管理
                        },
                        {
                            path: 'userAdd', 
                            component: systemUserAdd,
                        },
                        {
                            path: 'userInvite', //用户邀请列表
                            component: systemUserInvite,
                        },
                        {
                            path: 'invite', //邀请列表
                            component: systemInviteList,
                        }
                    ]
                }
            ]
        }
    ]
})
